import { zhiCardFactory as cardFactory } from '@ali/4ever-factory';
import createRenderNode from './createRenderNode';
import { ZhiCalendarCardPluginConfig } from './type';

const createZhiCalendarCardPlugin = cardFactory((config: { calendarCard?: ZhiCalendarCardPluginConfig }) => {
  const calendarCardConfig = config.calendarCard || {};
  return {
    cardType: 'calendar',
    isMobile: true,
    renderNode: createRenderNode({ ...calendarCardConfig, isMobile: true }),
  };
});

export default createZhiCalendarCardPlugin;
